import React, { Component } from 'react';

import './MyPost.css';

export default class MyPost extends Component {
	render() {
		return (
				<article className="MyPost">
					<h1>{this.props.title}</h1>
					<div className="Body">
						{this.props.content}
					</div>
					<p>{this.props.author}</p>
				</article>
		);
	}
}



